.Home__container {
    margin-top: 20vh;
    display: flex;
    padding-left: 115px;
    padding-right: 115px;
    position: relative;
    height: 80vh;


}

.Home__title {
    width: 50%;
    color: #FFF;
    font-size: 10vw;
    font-style: normal;
    font-weight: 600;
    line-height: 111.9%; /* 200.291px */
    pointer-events: none;
}

.Home__img {
   width: 50%;
   position: absolute;
   top: 0vh;
   right: 10vw;

}

.Home__img img {
    width: 60vw;
    transition: all 0.5s;
}

.Home__img img:hover, .Home__img img:focus {
    scale: 1.05;
}